<template>
	<div>
		<div class="back-title"><span>结算账号</span></div>
		<div class="pagebox" style="margin: 10px 0;">
			<Alert show-icon>同一个主体只允许绑定一个银行账号，平台会向该银行账号打一分钱验证账号信息 </Alert>
			<div class="form-box">
				<Form ref="formValidate" :model="formValidate" :rules="ruleValidate" :label-width="80">
					<FormItem label="结算类型">
						{{formValidate.income_type}}
					</FormItem>
					<FormItem label="开户名称">
						<span style="float: left;">{{formValidate.income_name}}</span>
						<div class="item-tips">开户名称必须和主体信息保持一致</div>
					</FormItem>
					<FormItem label="身份证号" prop="cardid">
						<Input v-model="formValidate.cardid" placeholder="请输入你的身份证号码" style="width: 400px;float: left;"></Input>
						<div class="item-tips">身份证号码必须和主体信息保持一致</div>
					</FormItem>
					<FormItem label="开户银行" prop="opening_bank">
						<Select v-model="formValidate.opening_bank" placeholder="请选择开户银行" style="width: 400px;">
							<Option :value="item.value" :key="item.value"  v-for="item in backlist">{{item.text}}</Option>
						</Select>
					</FormItem>
					<FormItem label="开户支行" prop="opening_branch">
						<Input v-model="formValidate.opening_branch" placeholder="请输入开户支行" style="width: 400px;float:left"></Input>
						<div class="item-tips">请填写开户支行全称，不能填写银行名称缩写，例如：中国邮政储蓄银行湖北分行街道口支行，如有疑问，可联系银行客服查询</div>
					</FormItem>
					<FormItem label="银行账号" prop="bank_account">
						<Input v-model="formValidate.bank_account" placeholder="请输入银行账号" style="width: 400px;float:left"></Input>
						<div class="item-tips">务必确保银行账号和开户名称对应无误</div>
					</FormItem>
					<FormItem label="邮箱地址" prop="mail">
						<Input v-model="formValidate.mail" placeholder="请输入结算单邮箱地址" style="width: 400px;float:left"></Input>
						<div class="item-tips">用于接收对账单信息</div>
					</FormItem>
					<FormItem label="" prop="interest">
			            <CheckboxGroup v-model="formValidate.interest">
			                <Checkbox label="我同意并遵守"></Checkbox><a>《内容开放平台推广合作协议》</a>
			            </CheckboxGroup>
			        </FormItem>
					<FormItem>
			            <Button type="primary" @click="handleSubmit('formValidate')">提交</Button>
			            <Button type="ghost" @click="handleReset('formDynamic')" style="margin-left: 8px">重置</Button>
			        </FormItem>
				</Form>
			</div>
		</div>
	</div>
</template>

<script>
  export default {
    data () {
      return {
        backlist: [],
        income_type: '对私账号',
        formValidate: {
          income_type: '对私账号',
          income_name: '科技男',
          cardid: '',
          opening_bank: '',
          opening_branch: '',
          bank_account: '',
          interest: []
        },
        ruleValidate: {
          name: [
            { required: true, message: '必填字段', trigger: 'blur' }
          ],
          cardid: [
            { required: true, message: '身份证号码不能为空', trigger: 'change' }
          ],
          opening_bank: [
            { required: true, message: '请选择开户银行', trigger: 'change' }
          ],
          opening_branch: [
            { required: true, message: '开户支行不能为空', trigger: 'blur' }
          ],
          bank_account: [
              { required: true, message: '银行账号不能为空', trigger: 'change' }
          ],
          mail: [
            { required: true, message: '结算单邮箱地址不能为空', trigger: 'blur' },
            { type: 'email', message: '邮件格式不正确', trigger: 'blur' }
          ],
          interest: [
            { required: true, type: 'array', min: 1, message: '请阅读《内容开放平台推广合作协议》', trigger: 'change' }
          ]
        }
      }
    },
    methods: {
      getbacklist () {
        this.$http.get('/static/js/back.json?page=', {emulateJSON: true}).then(function (res) {
          for (let i = 0; i <= res.data.data.length - 1; i++) {
            this.backlist.push(res.data.data[i])
          }
        })
      },
      handleSubmit (name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            this.$Message.success('Success!')
          } else {
            this.$Message.error('Fail!')
          }
        })
      }
    },
    created: function () {
      this.getbacklist()
    }
}
</script>

<style>

</style>


